<template>
<div class="passengerCorridor-box" id="passengerCorridor">

    <div class="leftlinemsg">
        <div class="tit-line">
            <div style="margin-right:0.2vw">途径公交线路</div>
            <div>站点</div>
        </div>
        <div class="zhandian-box">
            <div class="zhandian" v-for="(iteam,n) in slinedata" :key="n">
                <div class="zhnadiason" style="margin-right:0.2vw">{{iteam.name}}</div>
                <div class="zhnadiason zhnadiason1">
                    <div class='zhnadiasonla' @click="datastation(itam)" v-for="(itam,index) in (iteam.station)" :key="index">
                        {{itam.stationName}}
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div class="leftlinemsg leftlinemsg1">
        <div class="tit-line">
            <div style="margin-right:0.2vw">途径轨交线路</div>
            <div>站点</div>
        </div>
        <div class="zhandian-box">
            <div class="zhandian" v-for="(iteam,n) in mlinedata" :key="n">
                <div class="zhnadiason" style="margin-right:0.2vw">{{iteam.name}}</div>
                <div class="zhnadiason zhnadiason1">
                    <div class='zhnadiasonla' @click="datastation(itam)" v-for="(itam,index) in (iteam.station)" :key="index">
                        {{itam.stationName}}
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div class="lkicon">
        <div class="lkicon-box">
            <div class="lkicon-itam">
                <img style="margin-right:0.4vw" src="@/assets/image/icon_gj.png" />
                公交站点
            </div>
            <div class="lkicon-itam">
                <img style="margin-right:0.4vw" src="@/assets/image/icon_dt.png" />
                轨交站点
            </div>
        </div>
    </div>


    <div class="erach-box" id="echstation"  
      v-loading="echloadsd"
      element-loading-text="拼命加载中"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.6)"></div>
    
</div>


</template>

<script>
import MapMixin from '@/libs/myMap.js'
export default {
mixins: [MapMixin],
  components: {
  },
  data() {
    return {
      lineData:[],
      echloadsd:false,
      mlinedata:{},
      slinedata:{},
    };
  },
  beforeCreate() {},
  created() {
    // setTimeout(()=>{
    //   this.$store.commit('SET_LOADING',false)
    // },1000)
    
   this.getkyzlData()

  },
  mounted() {
       this.M_initMap('passengerCorridor')

      window.onresize = ()=> {
        this.myChart.resize()
      }
      
  },
  methods: {
    //站点显示信息窗口
    datastation(rows){
        // this.$emit('changefun',{
        // isinfobtn:rows
        // })
        this.searchStation(rows.stationName,[rows.longitude==undefined?rows.lon:rows.longitude,rows.latitude==undefined?rows.lat:rows.latitude]) 
    },
    initechart(data){
        this.myChart = this.$echarts.init(document.getElementById('echstation'));
        this.myChart.setOption({
        grid:{
            top:60,
            left:80,
            right:40,
            bottom:60,
        },
        tooltip:{
            trigger: 'axis',
            // formatter:'客流量：{c}人次',
            backgroundColor:'#144A8C',
            // borderWidth:0,
            // textStyle:{
            // color:'#D9EFFF',
            // }
        },
        legend:{
            top:'8%',
            left:"center",
            textStyle:{
                color:'#ffffff'
            }
        },
        title:{
            text:"客流量/人次",
            textStyle:{
            color:'#DAE4FF',
            fontWeight:'normal',
            fontSize:16,
            
            },
            top:26,
            left:10,
        },
        color:['#ffca40','#00a08a','#e20048','#447bd4','#2e16b1','#5ab91b','#d35f1a','#8c2eca'],
        xAxis: {
            type: 'category',
            boundaryGap:false,
            axisLabel:{
                interval:0,
                rotate:25 ,
                color:"#D9EFFF",
                borderType:"dashed",
                borderColor:"#194F95",
            
            },
            axisTick: {
                show:false
            },
            splitLine:{
                show:true,
                lineStyle:{
                color:'#194F95',
                type:'dashed'
                }
            },
            axisLine:{
                lineStyle:{
                color:'#194F95',
                type:'dashed'
                }
            },
            data: data[0].dataX
        },
        yAxis: {
        
            type: 'value',
            axisLabel:{
                color:"#D9EFFF",
                borderType:"dashed",
                borderColor:"#194F95",
            },
            splitLine:{
                show:true,
                lineStyle:{
                color:'#194F95',
                type:'dashed'
                }
            },
            
        },
        series: data
        });
        this.echloadsd=false
    },
    separateArr(data, n) {
        //获取要切割的数组的长度
        let len = data.length;
        let lineNum = len % n === 0 ? len / n : Math.floor(len / n + 1);
        let res = [];
        for (let i = 0; i < lineNum; i++) {
            // slice() 方法返回一个从开始到结束（不包括结束）选择的数组的一部分浅拷贝到一个新数组对象。且原始数组不会被修改。
            let temp = data.slice(i * n, i * n + n);
            res.push(temp);
        }
        return res;
    },
    getkyzlData(){
        this.$fetchGet("indicator/corridor").then(res => {
            let mdata=[],sdata=[],allStation=[],allStation1=[],zxtdata=[]
            for(let key  in res.result.mmap){
                let obj={} 
                obj.name=key
                obj.station=(res.result.mmap[key])
                obj.station.forEach(element => {
                    element.lnglat=[element.lon,element.lat]
                    element.style=0
                    allStation.push(element)
                });
                mdata.push(obj)
            }
            for(let key  in res.result.smap){
                let obj={} 
                obj.name=key
                obj.station=(res.result.smap[key])
                 obj.station.forEach(element => {
                     element.style=1
                     element.lnglat=[element.longitude,element.latitude]
                    allStation1.push(element)
                });
                sdata.push(obj)
            }
            // this.$store.commit('SET_KEYUNDATA', (allStation1.concat(allStation)))
            this.addGjMarker(allStation1.concat(allStation))
            // this.$store.commit('SET_KEYUNDATA1', (allStation))
            // console.log(this.$store.getters.keyunData)
            this.setTwo(res.result.corridorList)
            this.mlinedata=mdata
            this.slinedata=sdata
            for(let key  in res.result.corridorPassenger){
                let obj={} 
                obj.name=key
                obj.data=[]
                obj.dataX=[]
                 obj.type='line'
                obj.stack='总量'
                obj.smooth= true
                res.result.corridorPassenger[key].forEach(iteam=>{
                    obj.data.push(Number(iteam.passengerNum))
                    obj.dataX.push(iteam.passengerDate)
                })
                zxtdata.push(obj)
            }
            console.log(zxtdata)
            this.initechart(zxtdata)


        });
    },
    setTwo(data){
        let data2=[]
        data.forEach(iteam=>{
            iteam.lonlat1=this.setSz(iteam.lonlat.replace(/[\r\n]/g,"").split(' '))
            data2.push(iteam)
        })
        // this.$store.commit('SET_KEYUNDATA2',data2)
        this.addOverlayGroup4(this.passCorrline(data2))
    },
    setSz(baseArray){
        let len = baseArray.length;
        let n = 1; //假设每行显示4个
        let lineNum = len % n === 0 ? len / n : Math.floor( (len / n) + 1 );
        let res = [];
        for (let i = 0; i < lineNum; i++) {
            // slice() 方法返回一个从开始到结束（不包括结束）选择的数组的一部分浅拷贝到一个新数组对象。且原始数组不会被修改。
            let temp = baseArray.slice(i*n, i*n+n);
            let art=[]
            temp[0].split(',').forEach(iru=>{
                art.push(parseFloat(iru))
            })
            res.push(art);
        }
        
        return res

    },
    //
    removal(arr){
        var arr2 = arr.filter((x, index,self)=>{
            var arrids = []
            arr.forEach((item,i) => {
                arrids.push(item.stationName)
            })
            return arrids.indexOf(x.stationName) === index
        })  

        return arr2


    }

  }
};
</script>
